@import url('https://fonts.googleapis.com/css2?family=Bungee+Outline&family=Cinzel:wght@600&family=Kumar+One+Outline&family=Ledger&family=Londrina+Outline&family=Londrina+Shadow&family=Nova+Square&family=Quicksand:wght@300;500&family=Roboto:wght@100&family=Saira+Stencil+One&family=Train+One&display=swap');

.div-skills {
  display: flex;
}

#section-skills, #section-skills-black  {
  background-color: rgb(219, 219, 219);
  height: 100%;
  padding-top: 5%;
  transition: background 0.3s linear;
}

#section-skills-black {
  background-color: rgb(51,51,51);
  transition: background 0.3s linear;
}

#title-skills, #title-skills-black {
  font-family: 'Bungee Outline', cursive;
  text-align: center;
  font-size: 40px;
  color: rgb(51,51,51);
  transition: color 0.3s linear;
}

#title-skills-black {
  color: white;
}

#div-list {
  display: flex;
  height: 80%;
  align-items: center;
  justify-content: space-around;
  margin-top: 5%;
}

#list-skills {
  border-radius: 6px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  height: 90%;
  padding: 0;
  width: 60%;
}

.li-tecs, .li-tecs-black {
  background-color: rgb(145, 145, 145);
  display: flex;
  border-radius: 10px;
  font-family: 'Nova Square', cursive;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  height: 38%;
  text-align: center;
  width: 15%;
  margin: 1%;
  transition: background 0.3s linear;
}

.li-tecs-black {
  background-color: rgb(145, 145, 145);
  opacity: .5;
}

.li-tecs-black:hover, .li-tecs:hover  {
  transform: scale(1.05);
  cursor: pointer;
  opacity: 1;
}

.img-tec {
  margin: 9%;
  width: 80%;
}

#div-disc-tec, #div-disc-tec-black {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: 'Nova Square', cursive;
  font-size: 150%;
  height: 70%;
  width: 30%;
  color: rgb(51,51,51);
  transition: color 0.3s linear;
}

#div-disc-tec-black {
  color: white;
}

.div-hidden-skills {
  /* visibility: hidden; */
  margin-bottom: 3%;
}

@media (max-width: 768px) {
  #div-list {
    flex-direction: column;
    align-items: center;
  }

  #list-skills {
    width: 100%;
    /* height: 100%; */
  }

  .li-tecs, .li-tecs-black {
    /* width: 90%; Reduzindo a largura dos itens */
    margin: 5px 5px;
    text-align: center;
  }

  .img-tec {
    width: 50%; /* Reduzindo o tamanho das imagens */
  }

  #div-disc-tec, #div-disc-tec-black {
    width: 100%;
    margin-top: 20px;
    text-align: center;
  }
}

@media (max-width: 480px) {
  /* Estilos para telas menores, como dispositivos móveis de tamanho reduzido */
  #title-skills, #title-skills-black {
    font-size: 20px; /* Reduzindo o tamanho do título */
  }

  .li-tecs, .li-tecs-black {
    width: 25%;
    margin: 5px 5px;
    font-size: 12px; /* Reduzindo o tamanho do texto */
  }

  .img-tec {
    width: 30%; /* Reduzindo ainda mais o tamanho das imagens */
  }

  #div-disc-tec, #div-disc-tec-black {
    font-size: 100%; /* Reduzindo o tamanho do texto de descrição */
  }
}